import Vue from 'vue'
import Router from 'vue-router'
import My from './views/My'
import More from './views/More'
import MusicHall from './views/MusicHall'
import Song from './views/Song'
import SongList from './views/SongList'
import SingerList from './views/SingerList'
import Singer from './views/Singer'
import Love from './views/Love'
import PlayHis from './views/PlayHis'
Vue.use(Router)

const routes = new Router({
  routes: [
    {path:'/',redirect:'/musichall'},
    {path:'/my',name:'my',component:My},
    {path:'/more',name:'my',component:More},
    {path:'/musichall',name:'my',component:MusicHall},
    {path:'/songList',name:'song',component:Song},
    {path:'/songList/:id',name:'songList',component:SongList},
    {path:'/singerList/',name:'singerList',component:SingerList},
    {path:'/singer/:id',name:'singer',component:Singer},
    {path:'/loved',name:'love',component:Love},
    {path:'/playHis',name:'love',component:PlayHis},
    {path:'*',redirect:'/musichall'},
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ],
  linkActiveClass:'active'
})

export default routes