import axios from 'axios'

const playList = {
    state() {
        return {
            songList:JSON.parse(localStorage.getItem("songList"))||[], //播放列表
            index:Number(localStorage.getItem('index')||0), //索引
            mode:Number(localStorage.getItem('mode')||0), //播放模式  0,1,2=>循环，单曲循环，随机
            start:false,// 是否播放
        }
    },
    getters:{
        getCurPlayMusicInfo :(state,getters, rootState, rootGetters) =>{ //获取当前播放音信息
            let song = state.songList[state.index];
            if (song) {
                let music = {};
                if (song.id) {
                    let {
                        id,
                        mid ,//歌曲ID
                        name,//歌名
                        singer,
                        album
                    } = song;
                    music = {
                        id,
                        mid ,//歌曲ID
                        name,//歌名
                        singer,
                        album
                    }
                } else {
                    let {
                        songmid : mid ,//歌曲ID
                        songname : name ,//歌名
                        singer, //歌手
                        albumname,//专辑名
                    } = song.musicData;
                    music = {
                        id :mid,
                        mid ,//歌曲ID
                        name,//歌名
                        singer,
                         album:{
                            title:albumname
                         }
                    }
                }
                let isLoved = rootGetters[`musicLoved/isLoved`](music.mid);
                music.isLoved = isLoved;
                return music
            }else{
                return {}
            }
        },
    },
    mutations:{
        setDefalutList(state,songIds){
            //state.list = songIds;
           // localStorage.setItem("songList",songIds); 
        },
        setPlayList(state,{songList,index}){
            
            state.songList = songList;
            state.index = index;
            state.start = true;
            
            localStorage.setItem("songList",JSON.stringify(songList));
            localStorage.setItem("index",index);
            //console.log(songList)
          // console.log(id)
        },
        removeList(state,{song,type}){
            console.log(state)
            if (type == 1) {
                state.songList =   state.songList.filter(item=>{
                    return item.mid != song.mid
                })
            }
            if (type == 2) {
                state.songList = state.songList.filter(item=>{
                    return item.musicData.songmid != song.musicData.songmid
                })
            }
            localStorage.setItem("songList",JSON.stringify(state.songList));
           
        },
        setStart(state,start){//设置播放状态
            //console.log(start)
           state.start = start;
        },
        setIndex(state,num){
            if (num == -5) {
                let randomNum = Math.floor(Math.random()*state.songList.length)
                state.index = randomNum;
                localStorage.setItem('index',state.index )
                return;
            }
            let  nindex = Number(state.index) + Number(num)
            if ( nindex  > state.songList.length - 1) { 
                state.index = 0
                localStorage.setItem('index',state.index )
                return;
            }
            if (nindex == -1) {
                state.index = state.songList.length-1
                localStorage.setItem('index',state.index )
                return;
            }
            state.index = nindex;
            localStorage.setItem('index',state.index )

        },
        setNindex(state,nindex){
            state.index = nindex;
            localStorage.setItem('index',state.index )
        },
        setMode(state){
            let nmode = state.mode + 1;
            if (nmode > 2) {
                state.mode = 0
                localStorage.setItem('mode',state.mode )
                return;
            }
            state.mode = nmode;
            localStorage.setItem('mode',state.mode )
        }
    },
    actions:{
        getDefaultList(context){
            axios.get('/tencent/songList/hot?pageSize=0')
            .then(res=>{
                let dissid = res.data.data.list[0].dissid;
                axios.get(`/tencent/songList?id=${dissid}`)
                    .then(res=>{
                        let songIds = res.data.data[0].songids
                        context.commit('setDefalutList',songIds)
                    })
            }) 
        },
        
        
    },
    namespaced:true,
}

export default playList