<template>
  <div
    class="search-page"
    :class="{show:serachShow}"
  >
    <div class="search-header-container">
      <div class="search-header">
        <i
          class="iconfont icon-back"
          @click="hide"
        />
        <!-- <i
          class="iconfont icon-close"
          v-show="searchKey!=''"
        /> -->
        <input
          type="search"
          v-model="searchKey"
          class="search-ipt"
          placeholder="搜索音乐"
          @change="doSearch"
          @keydown="doSearch"
        >
         <!-- @change="doSearch" -->
      </div>
    </div>  
    <div class="search-main">
      <div v-show="seaHisList.length>0">
        <div class="search-history">
          <p class="title">
            搜索历史
          </p>
          <p
            class="text-right"
            @click="clearHis"
          >
            清空历史
          </p>
        </div>
        <ul
          class="history-list"
          v-for="item in seaHisList"
          :key="item.id"
        >
          <li class="history-list-item">
            {{ item }}<i class="iconfont icon-close" />
          </li>
        </ul>
      </div>


      <div
        class="search-list-container"
        v-show="searchEasyListShow"
      >
        <div class="search-tip">
          搜索 "<span class="key">{{ searchKey }}</span>"
        </div>
        <ul class="search-easy-list">
          <li
            class="search-list-item"
            v-for="item in serachPlacehold"
            :key="item.id"
            @click="comSearch(item.songname_hilight)"
          >
            <i class="iconfont icon-search" />
            <span v-html="item.songname_hilight" />
          </li>
        </ul>
      </div>

      <div
        class="search-result"
        style="display:none"
      >
        <ul class="search-result-tab clear-float">
          <li
            class="search-result-tab-item"
            @click="searchType=1"
          >
            <span :class="{active:searchType==1}">单曲</span>
          </li>
          <li
            class="search-result-tab-item"
            @click="searchType=2"
          >
            <span :class="{active:searchType==2}">视频</span>
          </li>
          <li
            class="search-result-tab-item"
            @click="searchType=3"
          >
            <span :class="{active:searchType==3}">专辑</span>
          </li>
          <li
            class="search-result-tab-item"
            @click="searchType=4"
          >
            <span :class="{active:searchType==4}">歌单</span>
          </li>
          <li
            class="search-result-tab-item"
            @click="searchType=5"
          >
            <span :class="{active:searchType==5}">歌手</span>
          </li>
          <li
            class="search-result-tab-item"
            @click="searchType=6"
          >
            <span :class="{active:searchType==6}">用户</span>
          </li>
          <li
            class="search-result-tab-item"
            @click="searchType=7"
          >
            <span :class="{active:searchType==7}">歌词</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import {mapState,mapMutations} from 'vuex'
import {searchAPI} from '@/api'

export default {
  name: 'Search',
  props:{
    serachShow:Boolean
  },
  data() {
    return {
      searchKey:"",
      searchEasyListShow:false,
      searchType:1,
      serachPlacehold:''
    }

  },
  computed: {
    ...mapState('searchHis',['seaHisList'])
  },
  methods: {
    ...mapMutations('searchHis',['setSearchHis','clearSearchHis']),
    hide(){
      this.$emit('hide');
    },
    clearHis(){
        this.clearSearchHis();
    },
    doSearch(){
     /// alert(11111111)
   /// return;
      if (this.searchKey != '') {
        this.axios.get(`${searchAPI}?keyword=${this.searchKey}&type=song&pageSize=10`)
          .then(res=>{
            this.searchEasyListShow = true;
            this.serachPlacehold = res.data.data.list;
           // console.log(this.serachPlacehold );
          })
      }else{
         // this.searchKey = ''
         this.searchEasyListShow = flase;
        // this.serachPlacehold = []
      }
    },
    comSearch(comSearchKey){
      let dd=comSearchKey.replace(/<\/?.+?>/g,"");
      let dds=dd.replace(/ /g,"");//dds为得到后的内容
      this.searchKey = dds;
      this.axios.get(`${searchAPI}?keyword=${dds}&type=song&pageSize=10`)
        .then(res=>{
            console.log(res)
        })
    }
  },
}
</script>
<style scoped lang="scss">

.show{
  transform:translateY(0) !important;
  opacity: 1 !important;
  z-index: 999 !important;
}
.search-page{
  position: fixed;
  bottom: 110px;
  width: 100%;
  top:0;
  background: #f4f4f4;
  transform: translateY(160px);
  opacity: 0;
  z-index: -1;
  transition: all 0.5s ,z-index 1s, opacity 1s;
  .search-main{
    position: absolute;
    top: 80px;
    bottom: 0;
    width: 100%;
    overflow: auto;
  }
  .search-header-container{
    position: fixed;
    height: 80px;
    background: $primary-color;
    .search-header{
      height: 80px;
      background: $primary-color;
      text-align: center;
      margin: 0 80px;
      line-height: 80px;
      position: relative;
      .search-ipt{
        border: 0 none;
        outline: none;
        background: $primary-color;
        color: #fff;
        font-size: $primary-font-size;
        display: block;
        height: 80px;
        line-height: 80px;
        width: 560px;
      }
      .search-ipt::placeholder{
        color: #bdf7da;
      }
      .iconfont{
        position: absolute;
        color: #fff;
        font-size: $primary-font-size;
      }
      .icon-back{
        left:-50px;
      }
      .icon-close{
        right: -50px;
      }
      
  }
  }
  .search-history{
      height: 80px;
      line-height: 80px;
      padding: 0 60px;
      .title{
        font-size: $primary-font-size;
        color: $light-text-color;
        float: left;
      }
      .text-right{
        font-size: $primary-font-size;
        color:$primary-color;
        float: right;
      }
  }
    .history-list{
    background: #fff;
    box-sizing: border-box;
    .history-list-item{
      padding: 0 60px;
      height: 100px;
      line-height: 100px;
      font-size: $primary-font-size;
      color: #000;
      border-top: 1px solid #eee;
    }
    .iconfont{
      float: right;
    }
  }
  .search-tip{
     color: $primary-color;
     font-size: $primary-font-size;
     height: 90px;
     line-height: 90px;
     margin: 0 33px;
  }

  .search-easy-list{
     .search-list-item {
       height: 90px;
       font-size: $primary-font-size;
       color: #000;
       line-height: 90px;
       border-bottom: 1px solid #eee;
       cursor: pointer;
       .iconfont{
         color: $light-text-color;
         font-size: $primary-font-size;
         display: inline-block;
         margin: 0 33px;
       }
       .cw {
       }
     }
  } 
  .search-result{
    overflow: hidden;
    .search-result-tab{
      text-align: center;
      height: 80px;
      line-height: 80px;
      color: $light-text-color;
      font-size: $primary-font-size;
      width: 100%;
      overflow: scroll;
      display: flex;
    }
    .search-result-tab-item{
      box-sizing: border-box;
      padding: 0 35px;
      flex: 0 0 auto;
      cursor: pointer;
      span{
        display: inline-block;
        height: 80px;
        line-height: 80px;
        box-sizing: border-box;
      }
      .active{
        color: $primary-color !important;
        border-bottom: 6px solid $primary-color !important;
      }
    }
  }
  
}



</style>
