
class Player{
    constructor(){
        this.audio = document.createElement("audio");
       // this.duration = 0;
        this.init();
    }
    init(){
        document.body.appendChild(this.audio);
    }
    play(src){
        if (this.audio.src== src ) {
            this.audio.play();
        }else{
            this.audio.src= src
            this.audio.addEventListener('canplay',(ev)=>{
                this.audio.play();
            })
        }
    }
    pause(){
        this.audio.pause();
    }
    on(event,callback){
        this.audio.addEventListener(event,(ev)=>{
            callback(ev);
        })
    }
}


export default Player