<template>
  <transition name="fade">
    <div class="singerList">
      <header class="singerList-header">
        <i
          class="iconfont icon-back"
          @click="back"
        />
        <p class="title">
          歌手
        </p>
      </header>
      <div
        class="singerList-main"
        ref="singerListMain"
      >
        <mt-index-list
          ref="singerList"
          v-show="singerList.length>0"
        >
          <mt-index-section
            v-for="(item, index) in mindex"
            :key="index"
            :index="item"
            ref="singerList2"
          > 
            <mt-cell
              v-for="(singer, i) in singerFilter(`${item}`)"
              :key="i"
              class="singer-item"
            >
              <div
                class="media"
                @click="toSinger(singer.singer_mid,singer.singer_pic,singer.singer_name)"
              >
                <img v-lazy="singer.singer_pic">
                <span>{{ singer.singer_name }}</span>
              </div>  
            </mt-cell>
          </mt-index-section>
        </mt-index-list> 
      </div>
    </div>
  </transition>
</template>
<script>
import {singerListAPI} from '@/api'
import pinyin from 'js-pinyin'
import { Indicator } from 'mint-ui';

pinyin.setOptions({checkPolyphone: false, charCase: 0});
export default {
  name: 'SingerList',
  data() {
    return {
      singerList :[],
      mindex: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split(""),
      mtHeight :0
    }
  },
  methods: {
    back(){
      this.$router.go(-1)
    },
    singerFilter(index){
      return this.singerList.filter(function (singer) {
         return pinyin.getFullChars(singer.singer_name).startsWith(index);
      })
    },
    getSingerList(){
        let page = 0;
        let pageSize = 100;
        Indicator.open('加载中...');
        this.axios.get(`${singerListAPI}?page=${page}&pageSize=${pageSize}`)
        .then(res=>{
            this.singerList = res.data.data;
             document.querySelector('.mint-indexlist-content').style.height = this.mtHeight;
             Indicator.close();
        }).catch(res=>{
           Indicator.close();
        })
    },
    toSinger(id,picUrl,singerName){
     // alert(111)
      this.$router.push({name:'singer',params:{id},query:{picUrl,singerName}});
    }
  },
  mounted() {
    this.getSingerList()
    this.mtHeight = this.$refs.singerListMain.offsetHeight + 'px'
   
  },
  computed: {
    
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .singerList{
    position: absolute;
    top:0;
    bottom: 0;
    width: 100%;
    z-index: 10;
    background: #fff;
     .singerList-header{
        height: 80px;
        align-items: center;
        position: fixed;
        width: 100%;
        z-index:11;
        line-height: 80px;
        background-repeat: no-repeat;
        background-size: 2000%;
        text-align: center;
        .title{
          color: #000;
          font-size: $primary-font-size;
        }
        .iconfont{
          color: #000;
          display: block;
          padding: 0 30px;
          font-size: 30px;
          position: absolute;
        }
      }
  }
  .singerList-main{
    position: absolute;
    top: 80px;
    background: #fff;
    width: 100%;
    bottom:110px;
    .singer-item{
		position: relative;
		height: 72px;
	}
	.media{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 80%;
		height: 72px;
		line-height: 72px;
		padding-left: 20px;
	}
	.media img{
		display: inline-block;
		width: 50px;
		height: 50px;
		margin-right: 15px;
		border-radius: 50%;
	}
	.media span{
    font-size: $primary-font-size;
    color: #000;
	}
  }
</style>
