import Vue from 'vue'
import Vuex from 'vuex'
import PlayList from './modules/playList'
import SearchHis from './modules/searchHis'
import MusicLoved from './modules/musicLoved'
import PlayHis from './modules/playHis'
Vue.use(Vuex)


const store = new Vuex.Store({
    modules:{
        playList :PlayList,
        searchHis :SearchHis,
        musicLoved:MusicLoved,
        playHis :PlayHis
    },
    strict: process.env.NODE_ENV !== 'production'
})

export default store


