

const playHis = {
    state(){
        return{
            playHisList :JSON.parse(localStorage.getItem("playHisList"))||[], //播放历史列表
            playHisMid:JSON.parse(localStorage.getItem('playHisMid'))||[]
        }
    },
    getters:{

    },
    mutations:{
        addPlayHis(state,music){
          //  console.log(music)
            if (state.playHisMid.indexOf(music.mid) > -1) {
                return;
            }
            state.playHisList.push(music);
            state.playHisMid.push(music.mid);
            localStorage.setItem('playHisList',JSON.stringify(state.playHisList));
            localStorage.setItem('playHisMid',JSON.stringify(state.playHisMid));
        }
    },
    actions:{

    },
    namespaced:true
}


export default playHis