<template>
  <div class="musicHall">
    <ul class="menu">
      <li
        class="menu-item"
        @click="toSingerList"
      >
        <a>
          <i class="iconfont icon-people" />
          <span>歌手</span>
        </a>
      </li>
      <!-- <li class="menu-item"  @click="$router.push('/top')">
          <a >
            <i class="iconfont icon-rank"></i>
            <span>排行</span>
          </a>
       </li> -->
      <li
        class="menu-item"
        @click="$router.push('/songList')"
      >
        <a>
          <i class="iconfont icon-category" />
          <span>歌单</span>
        </a>
      </li>
      <!-- <li class="menu-item">
           <a>
            <i class="iconfont icon-radio"></i>
            <span>电台</span>
         </a>
       </li> -->
      <li
        class="menu-item"
        @click="showErr"
      >
        <a>
          <i class="iconfont icon-headset" />
          <span>一起听</span>
        </a>
      </li>
    </ul>

    <div class="recommend">
      <h2 class="recommend-title">
        <span>为你推荐歌单</span>
        <!-- <i class="iconfont icon-right" /> -->
      </h2>
      <ul
        class="recommend-list"
        v-show="show1"
      >
        <li
          class="recommend-list-item"
          v-for="item in recommendSongList"
          :key="item.dissid"
        >
          <a
            class="bg-img"
            :style="{'background-image':`url(${item.imgurl}`}"
            @click="toSongList(`${item.dissid}`,`${item.imgurl}`)"
          >
            <i class="iconfont icon-headset headset">{{ formatNum(`${item.listennum}`) }}</i>
            <i class="iconfont icon-start more" />
          </a>
          <span>{{ item.dissname }}</span>
        </li>
      </ul>
      <ul
        class="recommend-list"
        v-show="!show1"
      >
        <li
          class="recommend-list-item"
          v-for="item in recommendSongList2"
          :key="item.dissid"
        >
          <a
            class="bg-img"
            :style="{'background-image':`url(${item.imgurl}`}"
            @click="toSongList(`${item.dissid}`,`${item.imgurl}`)"
          >
            <i class="iconfont icon-headset headset">{{ formatNum(`${item.listennum}`) }}</i>
            <i class="iconfont icon-start more" />
          </a>
          <span>{{ item.dissname }}</span>
        </li>
      </ul>
      <div class="recommend-bottom">
        <a
          class="btn"
          @click="show1=!show1"
        >
          <span><i class="iconfont icon-refresh" />换一批</span>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import {hotSongListAPI} from '../api'
import { Indicator } from 'mint-ui'
import {Toast} from 'mint-ui'
import util from '../util'

export default {
  name: 'MusicHall',
  data() {
    return {
      recommendSongList :[],
      recommendSongList2 :[],
      show1:true,
    }
  },
  mounted() {
    this.getRecommendSong();
  },
  methods: {
    showErr(){
      Toast({
                message: '功能不可用',
                duration: 5000
              });
        },
     formatNum(num){
       return util.formatNum(num)
     },
     getRecommendSong(){
       Indicator.open('加载中...');
       let page = Math.floor((Math.random()*100)+1); //随机获取第1-10页
       let length = 11; //获取6条
      // console.log(`${hotSongListAPI}?page=${page}&pageSize=${length}`)
       this.axios.get(`${hotSongListAPI}?page=${page}&pageSize=${length}`)
        .then(res=>{
          this.recommendSongList = res.data.data.list.slice(0,6)
          this.recommendSongList2 = res.data.data.list.slice(6,12)
          Indicator.close();
        }).catch(err=>{
          Toast({
          message: '网络异常',
          duration: 5000
        });
          Indicator.close();
        })
     },
     toSongList(id,imgUrl){
         this.$router.push({name:'songList',params:{id},query:{imgUrl}})
       },
       toSingerList(){
          this.$router.push(`/singerList`)
       }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.menu{
  display: flex;
  height: 160px;
  justify-content: center;
  .menu-item{
    flex: 1;
    display: flex;
    justify-content:center;
    align-items: center;
    cursor: pointer;
    a{
      display: block;
      text-align: center;
      .iconfont{
        color: $primary-color;
        font-size: 40px;
      }
      span{
        display: block;
        color: #000;
        font-size: $small-font-size;
      }
    }
  }
}
.recommend{
  text-align: center;
  .recommend-title{
    height: 100px;
    line-height: 100px;
    color: #000;
    font-size:36px;
    position: relative;
  }
  span{
    letter-spacing: 0.2em;
  }
  .icon-right{
    font-size: $primary-font-size;
    float: right;
    position: absolute;
    right: 35px;
  }
  .recommend-list{
    display: flex;
    flex-wrap: wrap;
    li:nth-of-type(3n){
     margin-right: 0 !important;
    }
    .recommend-list-item{
      flex: 0 0 236px;
      margin-right: 6px;
      margin-bottom: 30px;
    }
    a{
      display: block;
      width: 236px;
      height: 236px;
      background: gray;
      background-size: 100%;
      background-repeat: no-repeat;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      cursor: pointer;
      .more{
        font-size:35px;
        display: block;
        padding: 10px;
      }
      .headset{
        display: block;
        padding: 10px;
        font-size:25px;
      }
    }
    span{
      font-size: $small-font-size;
      display: block;
      text-align: left;
      padding-left: 0.2em;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
      width: 236px;
      box-sizing: border-box;
      height: 40px;
      line-height: 40px;
    }
  }
  .recommend-bottom{
    margin-bottom: 40px;
    .btn{
      display: block;
      height: 55px;
      width: 250px;
      line-height: 55px;
      border: 1px solid #cac6c6;
      margin: 0 auto;
    }
    span{
      color: #000;
      font-size: $primary-font-size;
      height: 55px;
      line-height: 55px;
      display: block;
    }
   .icon-refresh{
     color: #000;
      font-size: $primary-font-size;
      display: inline-block;
      padding-right: 20px;
   }
  }
}
</style>
