<template>
  <transition name="fade">
    <div class="songList">
      <header
        class="songList-header"
        :style="{'background-image':`url(${songListData.logo})`}"
        :class="{anop:anop}"
        ref="header"
      >
        <i
          class="iconfont icon-back"
          @click="back"
        />
        <p class="title">
          歌单
        </p>
      </header>
      <section
        class="songList-container"
        ref="container"
      >
        <div class="content">
          <a
            class="diss-pic"
            :style="{'background-image':`url(${songListData.logo})`}"
          />
          <div class="content-text">
            <p
              class="title"
              v-html="songListData.dissname"
            />
            <p class="cname">
              <a
                class="head-img"
                :style="{'background-image':`url(${songListData.headurl})`}"
              /><span v-html="songListData.nickname" />
            </p>
            <p
              class="memo"
              v-text="songListData.desc"
            />
          </div>
        </div>
        <ul class="btn">
          <li class="item">
            <i class="iconfont icon-love"  v-if="!isSongsLoved(id)" @click="addSongsLoved(songListData)"/>
            <i class="iconfont icon-love-fill" v-else  @click="removeSongsLoved(songListData)"></i>
          </li>
          <li class="item">
            <i class="iconfont icon-comment" /><span>{{ formatNum(songListData.cm_count) }}</span>
          </li>
          <li class="item">
            <i class="iconfont icon-share" /><span>分享</span>
          </li>
        </ul>
      </section>
      <div class="music-list-m">
        <div
          class="music-list-h"
          :style="sty"
          @click="playMusic(0)"
          v-if="songListData!=''"
        >
          <i class="iconfont icon-start" />
          <p>全部播放<span>({{ songListData.cur_song_num }})</span></p>
        </div>
        <div v-else class="noting">
        什么都没有
      </div>
        <!-- <ul class="music-list">
              <li class="music-list-item " v-for="(item, index) in songListData.songlist" :key="item.mid" @click="playMusic(index)" :class='{active:cIndex == index}'>
                <p class="index">{{index+1}}</p>
                <div class="content">
                    <p class="name">{{item.name}}</p>
                    <p class="singer"><span v-for="singer in item.singer" :key="singer.mid">{{singer.name}}<span>/</span></span><span>{{item.album.title||'空'}}</span></p>
                </div>
              </li>
            </ul> -->
            <MusicList :musiclist="this.songListData.songlist" ></MusicList>
      </div>
      <div class="bg-mask" />
      <div
        class="bg-mask-play"
        :style="{'background-image':`url(${songListData.logo})`}"
      />
    </div>
  </transition>
</template>
<script>
import { get } from 'http';
import {SongListDeatailAPI} from '@/api'
import { Indicator } from 'mint-ui'
import util from '@/util'
import {mapActions,mapMutations,mapState, mapGetters} from 'vuex'
import MusicList from  '@/components/musicList'

export default {
  name: 'SongList',
  data() {
    return {
      songListData:{},
      anop:false,
      id:'',
      cIndex :-1,
      sty:{
          position: '',
          top:''
      },
    }
  },
  props:['musiclist'],
  components:{
    MusicList
  },
  methods: {
    ...mapMutations('playList',['setPlayList']),
    ...mapMutations('musicLoved',['addSongsLoved','removeSongsLoved']),
    formatNum(num){
      return util.formatNum(num)
    },
    playMusic(index){ //播放音乐，更新store中的播放列表，和序号
        //console.log(song)
       // alert(111)
      // if (index == this.cIndex && this.start) {//单曲点击播放的歌曲和上次一样
      //   return;
      // }
      //  this.cIndex = index;
       let songList = this.songListData.songlist;
       console.log(songList)
       if (songList.length>1) {
       //  console.log(songList)
         this.setPlayList({songList,index})
       }
    },
    back(){
      this.$router.go(-1)
    },
    getSongListDetail(){
      this.id = this.$route.params.id;
      this.songListData = {};
      Indicator.open('加载中...');
      this.songListData.logo = this.$route.query.imgUrl
      this.axios.get(`${SongListDeatailAPI}?id=${this.id}`)
        .then(res=>{
          this.songListData = res.data.data[0];
        //  console.log(this.songListData.songlist)
          Indicator.close();
        })
    }
  },
  mounted() {
   this.getSongListDetail();
    window.addEventListener('scroll',()=>{
     //console.log(window.scrollY)
      if (window.scrollY >= this.$refs.header.offsetHeight*3) {
        this.anop = true;
      } else{
         this.anop = false;
      }
      if (window.scrollY >= this.$refs.container.offsetHeight) {
              this.sty.position = 'fixed';
              this.sty.top = this.$refs.header.offsetHeight + 'px';
      }else{
        this.sty.position = '';
        this.sty.top = '';
      }

    })
  },
  computed: {
    ...mapState('playList',['songList','index','start']), //播放列表歌曲
    ...mapGetters('musicLoved',['isSongsLoved'])
  },
  watch: {
    '$route': function(to,from){
      if (to.name == 'songList') {
        if (to.params.id != this.id) {
          this.getSongListDetail();
        } else{
          // if (window.scrollY >= this.$refs.container.offsetHeight) {
          //     this.sty.position = 'fixed';
          //     this.sty.top = this.$refs.header.offsetHeight + 'px';
          //   }else{
          //     this.sty.position = '';
          //     this.sty.top = '';
          //   }
        }
      }
    }
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.songList{
  position: absolute;
  top:0;
  bottom: 0;
  width: 100%;
  z-index: 10;
  .songList-header{
    height: 80px;
    position: fixed;
    width: 100%;
    z-index:11;
    // background-repeat: no-repeat;
    // background-size: cover;
    // background-position: 50%;
    background-repeat: no-repeat;
    background-size: 2000%;
    opacity:.5;
    transition: opacity 1s;
    line-height: 80px;
    text-align: center;
    .title{
      color: #fff;
      font-size: $primary-font-size;
    }
    .iconfont{
      color: #fff;
      display: block;
      padding: 0 30px;
      font-size: 30px;
      position: absolute;     

    }
  }
  .anop{
     opacity:1;
  }
  .songList-container{
    height: 360px;
    position: relative;
    top: 80px;
     z-index: 10;
     .content{
       margin: 0 50px;
       margin-top: 50px;
       height: 250px;
       display: flex;
       align-items: center;
       .diss-pic{
        height: 250px;
        background: gray;
        flex: 0 0 250px;
        background-size: cover;
        background-repeat: no-repeat;
       }
       .content-text{
         margin: 0 10px;
         p{
         color: #fff;
          }
          .title{
            font-size: 30px;
            width: 350px;
            @include text-ellipsis-one;
          }
          .cname{
            font-size: $primary-font-size;
            height: 60px;
            line-height: 60px;
            margin:  20px 0;
             width: 350px;
            @include text-ellipsis-one;
            .head-img{
              display: block;
              background-size: cover;
              height: 60px;
              width: 60px;
              border-radius: 50%;
              margin-right: 10px;
              float: left;
              background: gray;
              background-size: cover;
              background-repeat: no-repeat;
            }
          }
          .memo{
            font-size: $small-font-size;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            word-break: break-all;
            width: 360px;
            @include text-ellipsis-one;
          }
       }
       
     }
    .btn{
      display: flex;
      width: 100%;
      justify-content: center;
      text-align: center;
      margin-top: 55px;
      height: 55px;
      line-height: 55px;
      .item{
        flex: 1;
        height: 55px;
        line-height: 55px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .iconfont{
        color: #fff;
        font-size: 40px;
      }
      .icon-love-fill{
        color: #ee6867;
      }
      span{
          font-size: $small-font-size;
          color: #fff;
        }
    }
  }
  .bg-mask{
    position: absolute;
    top: 0;
    height: 520px;
    background: #292a2b;
    width: 100%;
  }
  .bg-mask-play{
    position: absolute;
    top: 0;
    height: 520px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 2000%;
    opacity:1;
    transform: translateZ(0);
    z-index: 9;
   // background-image: url('http://p.qpic.cn/music_cover/ESQkDB4ohgibFpRT23xHVFZLHzuRNNmsRYsmlOSRl5MZ6vWaHXU23vw/300?n=1')
  }
  .music-list-m{
    position: absolute;
    top: 520px;
    bottom: 110px;
    width: 100%;
    background: #fff;
    z-index: 5;
      .music-list-h{
        height: 100px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #eee;
        width: 100%;
        background: #fff;
        .iconfont{
          color: $primary-color;
          font-size: 40px;
          width:100px;
          text-align: center;
        }
        p{
          color: #000;
          font-size: $primary-font-size;
        }
      }
     
  }
  
  
}
</style>
