
export default {
    formatNum(num){
        if (num>10000) {
            let reg = /(?=(\B)(\d{3})+$)/g;
            let m = parseInt(num/10000) + ''
            return m.replace(reg,',') + "万"
        } else {
            return num;
        }
    },
    formatSeconds(s){
        let secondTime = parseInt(s);// 秒
        let minuteTime = 0;// 分
        if(secondTime > 60) {//如果秒数大于60，将秒数转换成整数
            //获取分钟，除以60取整数，得到整数分钟
            minuteTime = parseInt(secondTime / 60);
            //获取秒数，秒数取佘，得到整数秒数
            secondTime = parseInt(secondTime % 60);
            //如果分钟大于60，将分钟转换成小时
        }
        
        secondTime = secondTime < 10 ? "0" + secondTime : secondTime;
        minuteTime = minuteTime < 10 ? "0" + minuteTime : minuteTime;

        return minuteTime + ":" + secondTime;
    }
}
