const musicLoved = {
    state() {
        return{
            lovedList:JSON.parse(localStorage.getItem("lovedList"))||[],//喜欢音乐列表
            lovedMidList:JSON.parse(localStorage.getItem("lovedMidList"))||[],//喜欢音乐mid列表 
            songsLovedList:JSON.parse(localStorage.getItem("songsLovedList"))||[],//喜欢歌单列表
            songsLovedMidList:JSON.parse(localStorage.getItem("songsLovedMidList"))||[]//喜欢歌单MID列表 
        }
    },
    getters:{
        isLoved: state => mid => {
            return state.lovedMidList.indexOf(mid) > -1
        },
        isSongsLoved : state => mid => {
            return state.songsLovedMidList.indexOf(mid)> -1
        }
    },
    mutations:{
        addLoved(state,music){
            if (state.lovedMidList.indexOf(music.mid)>-1) {
                return
            }
           // console.log(music)
            state.lovedList.push(music);
            state.lovedMidList.push(music.mid)
            localStorage.setItem("lovedList",JSON.stringify(state.lovedList));
            localStorage.setItem("lovedMidList",JSON.stringify(state.lovedMidList));
        },
        removeLoved(state,music){
            state.lovedList = state.lovedList.filter(item =>{
                return item.mid != music.mid
            })
            state.lovedMidList =  state.lovedMidList.filter(item =>{
                return item != music.mid
            })
           localStorage.setItem("lovedList",JSON.stringify(state.lovedList));
           localStorage.setItem("lovedMidList",JSON.stringify(state.lovedMidList));
        },
        addSongsLoved(state,songs){
            if (state.songsLovedMidList.indexOf(songs.disstid)>-1) {
                return
            }
            state.songsLovedList.push(songs);
            state.songsLovedMidList.push(songs.disstid)
            localStorage.setItem("songsLovedList",JSON.stringify(state.songsLovedList));
            localStorage.setItem("songsLovedMidList",JSON.stringify(state.songsLovedMidList));
        },
        removeSongsLoved(state,songs){
            state.songsLovedList = state.songsLovedList.filter(item =>{
                return item.disstid != songs.disstid
            })
            state.songsLovedMidList =  state.songsLovedMidList.filter(item =>{
                return item != songs.disstid
            })
            localStorage.setItem("songsLovedList",JSON.stringify(state.songsLovedList));
            localStorage.setItem("songsLovedMidList",JSON.stringify(state.songsLovedMidList));
        }
    },
    actions:{},
    namespaced:true
}

export default musicLoved