<template>
  <div class="playTool">
    <div class="play-bar">
      <div class="container">
        <div
          v-if="songList != ''"
          class="ea"
          @click="showPalyPage=true"
        >
          <img
            :src="getImgpath"
            :class="{runing:start}"
          >
          <div class="warp">
            <a>{{ getCurPlayMusicInfo.name }}</a><br>
            <span>{{ getCurPlayMusicInfo.singer[0].name }}</span>
          </div>
        </div>
        <div
          class="defalt"
          v-if="songList == ''"
        > 
          QQ音乐 让生活充满音乐
        </div>
        <i
          class="iconfont icon-music-list ml"
          @click="isShowMenu"
        />
        <i
          class="iconfont"
          @click="playfn"
          :class="{'icon-start':!start,'icon-stop':start}"
        />
      </div>
    </div>
    <transition name="fade">
      <div
        class="playPage"
        :class="{show:showPalyPage}"
        v-show="showPalyPage"
      >
        <div class="bg-mask"   v-show="showPalyPage">

          <img :src="getImgpath" >

        </div>


        <div
          class="playPage-main"
          ref="playPageMain"
        >
          <div
            class="play-header"
            ref="playHeader"
          >
            <div class="play-header-t">
              <i
                class="iconfont icon-down"
                @click="showPalyPage=false"
              />
              <h1>{{ getCurPlayMusicInfo.name }}</h1>
            </div>
            <div class="play-header-b">
              <p v-if="getCurPlayMusicInfo.singer">
                {{ getCurPlayMusicInfo.singer[0].name }}
              </p>
            </div>
          </div>
          <div
            class="play-main"
            ref="palyMain"
          >
            <div
              class="cover-img"
              ref="bgImg"
              :style="{'background-image':`url(${getImgpath})`}"
              :class="{runing:start}"
            />
             <!-- <div class="lrc">
              <p>1111111111111111111</p>
            </div> -->
          </div>
          <div
            class="bottom-tool"
            ref="playBot"
          >
           

            <div class="play-progress">
              <div class="text">
                {{ curTime }}
              </div>
              <div class="progress">
                <div
                  class="slide"
                  ref="slide"
                  draggable="true"
                />
                <div
                  class="progress-t"
                  @click="setPlayTime"
                  ref="progressA"
                >
                  <div
                    class="progress-b"
                    ref="progressB"
                  />
                </div>
              </div>
              <div class="text">
                {{ endTime }}
              </div>
            </div>
            <ul class="play-btn">
              <li class="play-btn-item">
                <i
                  class="iconfont small "
                  :class="{'icon-loop':mode==0,'icon-loop-one':mode==1,'icon-random':mode==2}"
                  @click="setMode"
                />
              </li>
              <li class="play-btn-item">
                <i
                  class="iconfont middle icon-prve"
                  @click="setIndex(-1)"
                />
              </li>
              <li class="play-btn-item">
                <i
                  class="iconfont big "
                  @click="playfn"
                  :class="{'icon-start':!start,'icon-stop':start}"
                />
              </li>
              <li class="play-btn-item">
                <i
                  class="iconfont middle icon-next"
                  @click="setIndex(1)"
                />
              </li>
              <li class="play-btn-item">
                <i
                  class="iconfont small small icon-menu"
                  @click="maskShow=true"
                />
              </li>
            </ul>
            <ul class="bottom-btn">
              <li class="bottom-btn-item">
                <i
                  class="iconfont icon-love"
                  v-if="!getCurPlayMusicInfo.isLoved"
                  @click="addLoved(getCurPlayMusicInfo)"
                />
                <i
                  class="iconfont icon-love-fill"
                  v-if="getCurPlayMusicInfo.isLoved"
                  @click="removeLoved(getCurPlayMusicInfo)"
                />
              </li>
              <li class="bottom-btn-item">
                <i class="iconfont icon-xiazai" />
              </li>
              <li class="bottom-btn-item">
                <i class="iconfont icon-share" />
              </li>
              <li class="bottom-btn-item">
                <i class="iconfont icon-comment"></i>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </transition>

    <!-- 遮罩 -->
    <transition name="fade">
      <div
        class="mask"
        v-show="maskShow"
        @click="maskShow=false"
      />
    </transition>

    <transition name="fade">
      <div
        class="sing-list"
        v-show="maskShow"
      >
        <div class="top">
          <a>
            <i
              class="iconfont"
              @click="setMode"
              :class="{'icon-loop':mode==0,'icon-loop-one':mode==1,'icon-random':mode==2}"
            />
            <p v-if="mode == 0">顺序播放({{ songList.length }}首)</p>
            <p v-if="mode == 1">单曲循环</p>
            <p v-if="mode == 2">随机播放({{ songList.length }}首)</p>
            <i class="iconfont icon-dustbin" />
          </a>
        </div>
        <ul
          class="sing-list-m"
          ref="mList"
        >
          <li
            v-for="(song,curndex) in songList "
            :key="curndex"
            @click="setNindex(curndex)"
            :ref="`item${curndex}`"
          >
            <a
              v-if="song.id"
              :class="{'active':getCurPlayMusicInfo.mid == song.mid}"
            >
              <p class="title">{{ song.name }}</p>
              <p class="name">-{{ song.singer[0].name }}</p>
              <i class="iconfont icon-dustbin" @click.stop="removeList({song,type:1})"/>
            </a>
            <a
              v-else
              :class="{'active':getCurPlayMusicInfo.mid == song.musicData.songmid}"
            >
              <p class="title">{{ song.musicData.songname }}</p>
              <p class="name">-{{ song.musicData.singer[0].name }}</p>
              <i class="iconfont icon-dustbin" @click.stop="removeList({song,type:2})"/>
            </a>
          </li>
        </ul>
        <div
          class="bottom"
          @click="maskShow = false"
        >
          <p>关闭</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import {mapState,mapGetters,mapMutations} from 'vuex'
import {Toast} from 'mint-ui'
import Player from '@/player.js'
import {musicPicAPI,musicUrlAPI} from '@/api'
import util from '@/util'
import { constants } from 'crypto';
//import Darg from '@/drag'
// let player = new QMplayer({ target:'web'});//初始化播放组件
var player = new Player();
export default {
  name: 'PlayTool',
  data() {
    return {
       showPalyPage:false,
       maskShow:false,
       palyMainHeight:0,
       cIndex :0,
       musicTotolTime:0,
       curTime:'00:00',
       endTime:'00:00',
       slide:''
    }
  },
   computed: {
    ...mapState('playList',['songList','index','start','mode']), //播放列表歌曲
    ...mapGetters('playList',['getCurPlayMusicInfo']), //获取当前播放歌曲id
    getImgpath(){
      return  `${musicPicAPI}?id=${this.getCurPlayMusicInfo.mid}`
    },
  },
   methods: {
     ...mapMutations('playList',['setStart','setIndex','setMode','setNindex','removeList']),       // 设置播放状态
     ...mapMutations('musicLoved',['addLoved','removeLoved']),
     ...mapMutations('playHis',['addPlayHis']),
     isShowMenu(){
        if (JSON.stringify(this.getCurPlayMusicInfo) === '{}') {
          return;
        }
        this.maskShow = true;
     },
     playfn(){
      //console.log(this.getCurPlayMusicInfo.mid)
      if (JSON.stringify(this.getCurPlayMusicInfo) === '{}') {
          Toast({
            message: '没有要播放的歌曲',
            position: 'middle',
            duration: 5000
          });
        }else{
           this.setStart(!this.start);
        }
     },
     playMusic(){//播放音乐
        this.setStart(true);
     
        if (player.state == 'playing' &&player.data.song.mid == this.getCurPlayMusicInfo.mid) {
          return;
        }
        player.play(`${musicUrlAPI}?id=${this.getCurPlayMusicInfo.mid}`); //播放
        this.addPlayHis(this.getCurPlayMusicInfo) //播放历史保存到store
   
    },
    stopMusci(){
       player.pause();//暂停播放
       this.setStart(false);
    },
    playMainHeight(){
      if (this.showPalyPage == true) { //设置中间部分的高度
        let a = this.$refs.playPageMain.offsetHeight - this.$refs.playHeader.offsetHeight - this.$refs.playBot.offsetHeight;
        this.$refs.palyMain.style.height = a + 'px'
       if (a > this.$refs.palyMain.offsetWidth) {
         this.$refs.bgImg.style.height = this.$refs.palyMain.offsetWidth + 'px'
         this.$refs.bgImg.style.width = this.$refs.palyMain.offsetWidth + 'px'
        }else{
          this.$refs.bgImg.style.height = this.$refs.palyMain.offsetHeight + 'px'
         this.$refs.bgImg.style.width = this.$refs.palyMain.offsetHeight + 'px'
        }
      } 
    },
    setPlayTime(ev){
      let tWidth = this.$refs.progressA.offsetWidth;
      let cWidth =  ev.offsetX;
      let pec = cWidth/tWidth;
      player.audio.currentTime = this.musicTotolTime*pec;
      this.setStart(true);
      //progressA
      //let  = ev.clientX
      //console.log(pec)
    }
   },
   mounted() {
       window.addEventListener('popstate', (event)=> {
         if ( this.showPalyPage) {
           this.showPalyPage = false;
           history.pushState(null, null, location.href );
         }
        }); 

      player.on("error", (err) => { //监听播放错误
        console.log(err)
        Toast({
            message: err.message,
            position: 'middle',
            duration: 5000
        });
        //this.setIndex()
        this.setStart(false);
      })

      player.on("timeupdate", (ev)=> {//监听播放位置

        if (player.audio.duration) {
          this.musicTotolTime = player.audio.duration;
          this.curTime =  util.formatSeconds(player.audio.currentTime)
          this.endTime = util.formatSeconds(player.audio.duration)
          let per =  player.audio.currentTime/this.musicTotolTime * 100 + "%";
          this.$refs.slide.style.left = per;
          this.$refs.progressB.style.width = per;
        }
      })
      player.on("waiting", (ev)=> {//监听在缓冲数据
        console.log(ev)
      })

      player.on("ended", (ev)=> {//监听播放停止
      //  console.log(ev)
        this.musicTotolTime = 0;
        this.endTime = util.formatSeconds(0)
        if (this.mode === 0) { //循环
           this.setIndex(1)
          return;
        }
        if (this.mode === 1) {// 单曲循环
            this.setIndex(0)
            player.play(`${musicUrlAPI}?id=${this.getCurPlayMusicInfo.mid}`);
           return;
        }
        if (this.mode === 2) {//随机
            this.setIndex(-5)
           return;
        }
      })
      window.addEventListener('resize',()=>{
         this.playMainHeight();
      })
   },
   updated() {
      this.playMainHeight();
      if (this.showPalyPage || this.maskShow) {
         document.body.style.overflow = 'hidden'
        if (this.maskShow) {
          if (this.songList.length == 0) {
            this.maskShow = false;
            return
          }
          this.$refs.mList.scrollTop = this.$refs[`item${this.index}`][0].offsetTop;
        }
      }else{
          document.body.style.overflow = ''
      }
   },
 
  watch:{
    start : function(_start){ //监听播放状态变化
      if (_start) {
        this.playMusic();
      } else {
        this.stopMusci();
      }
    },
    // index : function(_index){   //监听播放索引变化
    //    this.playMusic();
    //  // }
    // },
    getCurPlayMusicInfo : function () {
     // alert("改变了")
       this.playMusic();
    }
    // songList:function(){
    //   this.playMusic();
    // }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.runing{
    animation-play-state:running !important;
  }
.play-bar{
  position: fixed;
  z-index: 13;
  height: 110px;
  bottom: 0;
  width: 100%;
  box-shadow: 0 -2px 2px  #eee;
  background: #fff;
  .container{
    padding: 15px 30px;
    box-sizing: border-box;
    height: 100%;
    line-height: 80px;
    .defalt{
      height: 80px;
      line-height: 80px;
      font-size: $primary-font-size;
      color: #000;
      margin-left: 15px;
      width: 420px;
      float: left;
    }
    .ea{
      width: 420px;
      float: left;
        img{
          height: 75px;
          width: 75px;
          border-radius: 50%;
          float: left;
          background: gray;
          animation: rotating 20s linear infinite;
          animation-play-state:paused;
        }
        
        .warp{
          display: inline-block;
          width: 330px;
          position: relative;
          height: 100%;
        }
        a{
          color: #000;
          font-size: $primary-font-size;
          position: absolute;
          top: -23.5px;
          left:22px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
          width: 330px;
        }
        span{
          color:$light-text-color;
          font-size: $primary-font-size;
          position: absolute;
          bottom: -23.5px;
          left: 22px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
          width: 330px;
        }

    }
    
    i{
      float: right;
      font-size: 60px;
      color: $primary-color;
    }
    .ml{
      margin-left: 50px;
    }
  }
}
//  .playPage-main{
//     position: absolute;
//     top:0;
//     bottom: 0;
//     width: 100%;
//     background-size: cover;
//   }

.playPage{
  position: fixed;
    top:0;
    bottom: 0;
    width: 100%;
    background: #292a2b;
    z-index: 14;
  .playPage-main{
    position: absolute;
    //height: 100%;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 15;
    }
  .play-header{
    height: 120px;
    .play-header-t{
      height:80px;
      text-align: center;
      line-height: 80px;
       margin: 0 80px;
       position: relative;
      h1{
        font-size: 32px;
        color: #fff;
        box-sizing: border-box;
        @include  text-ellipsis-one;
        padding: 0 60px;
      }
      .icon-down{
        position: absolute;
        font-size: 32px;
        color: #fff;
        left: -10px;
      }
    }
    .play-header-b{
      height: 40px;
      line-height: 40px;
      text-align: center;
      p{
         font-size:$small-font-size;
        color: #fff;
      }
    }
  }
  .play-main{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:0 80px;
    box-sizing: border-box;
    flex-wrap: wrap;
    .cover-img{
      background: gray;
      border-radius: 50%;
      animation: rotating 20s linear infinite;
      background-size: cover;
      background-repeat: no-repeat;
      animation-play-state:paused;
    }
    @keyframes rotating {
      from {transform: rotate(0);}
      to{transform: rotate(360deg)}
    }
     .lrc{
      text-align: center;
      color: #fff;
      font-size: $primary-font-size;
      margin-bottom: 20px;
    }
  }
  .bottom-tool{
    position: absolute;
    width: 100%;
    bottom: 0;
    padding-top: 15px;
   
    .play-progress{
      margin: 0 40px;
      display: flex;
      justify-content: center;
      padding: 30px 0;
      .progress{
         flex:  1;
         height: 5px;
         margin: 0 65px;
         position: relative;
        .slide{
          position: absolute;
          left: 0;
          height: 30px;
          width: 30px;
          border-radius: 50%;
          background: #fff;
          top:-12.5px;
        }
          .progress-b{
            background: $primary-color;
            width: 0;
            height: 5px;
          } 
          .progress-t{
            background: #74716c;
            height: 5px;
          }
      }
      .text{
        height: 5px;
        line-height: 5px;
        font-size: $small-font-size;
        color: #fff;
        flex: 0;
      }
    }
    .play-btn{
      display: flex;
      justify-content: center;
      align-items: center;
      margin:20px 0;
      .play-btn-item{
        flex: 1;
        text-align: center;
      }
      .small{
        font-size: 40px;
        color:#fff;
      }
      .middle{
        font-size: 80px;
         color: #fff;
      }
      .big{
        font-size: 100px;
        color: #fff;
      }
    }
    .bottom-btn{
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 30px;
      .bottom-btn-item{
        flex: 1;
        text-align: center;
      }
      .iconfont{
        color: #fff;
        font-size: 50px;
      }
      .icon-love-fill{
        color: #ee6867;
      }
      .icon-comment{
        position: relative;
        display: inline-block;
        span{
          position: absolute;
          font-size: 8px;
          top: 0;
          left:0;
        }
      }

    }

  }
  .bg-mask{
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;
      background: rgba(127,127,127,.5);
      z-index: 12;
      //background-size: cover;
      //background-repeat: no-repeat;
     // background-position: 50%;
      //filter: blur(10px);
      //opacity: 0.6;
      //display: none
      img{
       //height: 100%;
        filter: blur(30px);
        opacity: 0.4;
        height: 100%;
        object-fit: cover;
      }
    }
    
}

.sing-list{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 55%;
  z-index: 20;
  color:  $primary-text-color;
  box-sizing: border-box;
  .top{
     height: 110px;
     line-height: 110px;
      background:rgba(27,34,44,.95);
      a{
         display: block;
         margin: 0 60px;
         .iconfont{
          font-size: 40px;
          float: left;
        }
        p{
          font-size: $primary-font-size;
          float: left;
          margin-left: 30px;
        }
        .icon-dustbin{
          float: right;
        }
      }
  }
  .sing-list-m li:last-of-type{
    border-bottom: 1px solid rgba(127,127,127,.5);
  }
  .sing-list-m{
    position: absolute;
    overflow: scroll;
    bottom: 110px;
    top: 110px;
    width: 100%;
    background:rgba(30,37,47,.95);
    .active{
      color: $primary-color !important;
      .name{
         color: $primary-color !important;
      }
    }
    li{
       height: 90px;
       line-height: 90px;
       border-top: 1px solid rgba(127,127,127,.5);
    }
    a{
      display: block;
      padding: 0 60px;
      width: 100%;
      height: 90px;
      box-sizing: border-box;
      .title{
        font-size: $primary-font-size;
        float: left;
        @include text-ellipsis-one;
        max-width: 200px;
      }
      .name{
        color: $light-text-color;
        font-size: 24px;
         float: left;
         margin-left: 20px;
          @include text-ellipsis-one;
          width: 260px;
      }
      .icon-dustbin{
        font-size: 40px;
        float: right;
        }
    }
  }
  .bottom{
    position: absolute;
    bottom: 0;
    height: 110px;
    width: 100%;
    background:rgba(30,37,47,.95);
    text-align: center;
    font-size: $primary-font-size;
    line-height: 110px;
    cursor: pointer;
  }
}
.mask{
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  background: rgba(127,127,127,.5);
  z-index: 15;
}
</style>
